/********************Header*************************/

.bg-image {

    position: relative;
    background: #1a1c2b url(/portfolio/static/media/background.89777a9a.jpg) no-repeat;
    background-size: contain;
    background-position: center;
    height: 90vh;
    overflow: hidden;

}

.bg-text {
    background-color: rgba(0, 0, 0, 0.65);
    color: #f9ab00;
    color: var(--primary-orange);
    font-family: "Cormorant Garamond", serif;
    font-weight: 700;
    height: 20vh;
    display: flex;
    flex-direction:column;
    justify-content: center;
    left: 50%;
    position: absolute;
    text-align: center;
    text-transform: uppercase;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 60vw;
    z-index: 2;
    border: 0.09rem solid #eee;
    border-radius:0.5rem;
    border-image: none;
    border-image: initial;
    padding: 20px 0px;
}



.typed-text {
    text-transform: capitalize;
    color: #eee;
    letter-spacing: 1.5px;
}



.tsparticles-canvas-el {
    position: absolute;
    height: 100vh;
    width: 100vw;
    z-index: 1;
}

nav {
    z-index: 2;
}

/********************Common Update*************************/



.title_heading {
    text-align: center;
    color: rgb(141, 133, 133);
    font-family: 'Josefin Sans', sans-serif;
}
.about__main {
    background-color: #1a1c2b;
    height: 100vh;
    padding-bottom: 30px;
}

h1.aboutme-tite {
    text-align: center;
    color: whitesmoke;  
    text-decoration: underline;
}

.pic-container .img-fluid {
    border: #f9ab00 solid 0.5px;
    border-radius: 30px;
    box-shadow: #0011f9 0 25px 80px -15px;
    height: 50%;
    width: auto;
}

.pic-container {
    display: flex;
    justify-content: center;
    padding-top: 40px;
}

.navbar-light .navbar-toggler-icon {
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 30 30'%3e%3cpath stroke='rgba%28255, 165, 0, 0.8%29' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e");
}

.navbar-light .navbar-toggler {
    border-color: #f9ab00;
    border-radius: 2px;

}

#About .title_heading {
    margin-bottom: 50px;
}

.about_content {
    padding-top: 50px;
    padding-bottom: 0;
}

.name {
    font-family: 'Grape Nuts', cursive;
    letter-spacing: 1px;
    color: #f9ab00;
    font-size:2.2rem;
    font-weight: 600;
}

.CVbtn {
    background-color: #f9ab00;
    color: black;
    padding: 8px;
    border: rgba(255, 167, 4, 0.852) solid 1px;
    border-radius: 0.5rem;
    font-weight: 500;
}

.CVViewbtn {
    background-color: #1a1c2b;
    color: white;
    padding: 9px 30px;
    margin: 0px 20px;
    margin-left: 0;
    border: #f9ab00 solid 0.01rem;
    border-radius: 0.5rem;
    font-weight: 500;
}

.CVViewbtn:hover {
    background-color: #f9ab00;
    color: black;
}

.CVbtn:hover {
    background-color: var(--primary);
    
}

h3 {
    color: whitesmoke;
    text-align: start;
    word-spacing: 3px;

}

p {
    color: whitesmoke;
    line-height: 35px;
    font-size: 20px;
}

@media screen and (max-width:576px) {
    .pic-container .img-fluid {
        height: auto;
        width: 70%;
    }
}

@media screen and (min-width: 576px) and (max-width:768px) {
    .pic-container .img-fluid {
        height: auto;
        width: 100%;
    }
}

@media screen and (max-width:768px) {

    .about__main {
        height: auto;
    }

    .CVbtn {
        display: none;
    }
    .CVViewbtn {
        padding: 5px 10px;   
    }

    .about__heading {
        display: none;
    }

    .pic-container {
        padding-bottom: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    p {
        color: whitesmoke;
        line-height: 30px;
        font-size: 18px;
    }
}
.contact_main {
    background-color:rgb(37, 36, 78);
    background: linear-gradient(#1a1c2b00,rgba(0, 0, 0, 0.711)), url(/portfolio/static/media/footer.45e94473.png) no-repeat center/cover #1a1c2b;
    height: 40vh;
}
#Contact .title_heading {
    padding: 2.8rem 0 1rem 0;
    color: #fff;
}

.contact_link {
    font-size:3rem;
    margin:0 1rem;
}

.contact_link i{
    color: #eee;
    transition: .5s;
}
.contact_link i:hover{
    color: #f9ab00;
    -webkit-transform:translateY(-0.3rem);
            transform:translateY(-0.3rem);
    cursor:pointer;
}   



@media screen and (max-width:768px) {
    .contact_main {
        height: 25vh;
    }

    .contact_main .img-fluid {
        height: auto;
        width: 85%;
    }

    .contact_main .col-sm-3 {
        padding-right: 50px;
    }

    .contact_main .col-3 {
        flex: 0 0 auto;
        width: 30%;
    }
}

@media screen and (min-width:768px) {
    .contact_main .img-fluid {
        height: auto;
        width: 30%;
        transition:0.5s;
    }

    .contact_main .img-fluid:hover {
        -webkit-transform:translateY(-10px);
                transform:translateY(-10px);
    }

    .contact_main .col-3 {
        flex: 0 0 auto;
        width: 27%;
    }
}

@media screen and (max-width:480px) {
    .contact_link {
        font-size:2rem;
        margin:0 0.5rem;
    }
    .contact_main{
        height:25vh;
    }
    #Contact .title_heading {
        padding: 1.8rem 0 0.6rem 0;
    }
}
.project_main {
    background-color: #1a1c2b;
    height: auto;
}

.project_main .title_heading {
    padding-top: 50px;
    padding-bottom: 50px;
}

.project_img {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #172855;
}

.img-fluid {
    height: auto;
    width: 95%;
}

.card{
    max-width:560px;
    border: 1px solid purple;
}
.card-body{
    background-color: lightsteelblue;
}

.card-section {
    display: flex;
    justify-content: start;
    padding-bottom: 35px;
}

.card:hover {
    border: 1px solid black;
    box-shadow: 0 0 20px purple;
}

.card-section:nth-child(3) {
    justify-content: center;
}

.card-section:nth-child(4) {
    justify-content: end;
}

.card-title {
    font-size: 25px;
    text-align: center;
    font-weight: 700;
}

.card-text {
    color: #1a1c2b;
    line-height: normal;
}

#projects .item {
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 800px;
    margin: 0 auto;
    margin-bottom: 15rem;
}

#projects .item:nth-child(even) {
    flex-direction: row-reverse;
}

#projects .item .left {
    flex: 1 1;
}

#projects .item .right {
    flex: 1 1;
    margin-left: -150px;
}

#projects .item {
    height: 50%;
    width: 50%;
}

.project-desc {
    color: black;
}

#projects .img {
    height: auto;
    width: 50%;
}

.view_btn {
    background-color: whitesmoke;
    color: white;
    padding: 6px 12px;
    border: black solid 1px;
    border-radius: 20px;
    font-weight: 500;
    color: black;
    font-size: 1rem;
}

.view_btn:hover {
    background-color: #1a1c2b;
    color: white;
}




@media screen and (max-width: 576px) {
    .img-fluid {
        height: auto;
        width: 100%;
    }
    .card{
        max-width:350px
    }
    .card-section:nth-child(even) {
        display:flex;
        justify-content: center;
        align-items: center;
    }
    
    .card-section:nth-child(odd) {
        display:flex;
        justify-content: center;
        align-items: center;
    }
}
.Education_main {
    background-color: #1a1c2b;
    height: auto;
    padding-bottom: 30px;
    padding-top: 10px;
    margin: -1px 0 -3px 0;
}

#Education h3 {
    color: whitesmoke;
    font-weight: bold;
}

#Education .title_heading {
    margin-bottom: 50px;
}

.data:nth-child(1) {
    text-align: end;
}

.data:nth-child(1) h3 {
    text-align: end;
}

.qualification_data {
    display: grid;
    grid-template-columns: 1fr -webkit-max-content 1fr;
    grid-template-columns: 1fr max-content 1fr;
    grid-column-gap: 2.5rem;
    -webkit-column-gap: 2.5rem;
            column-gap: 2.5rem;
    margin-bottom: 10px;
}

.qualification_subtitle {
    display: inline-block;
    font-size: 22px;
    font-weight: 400;
    color: white;
}

.qualification_calendar {
    font-size: 20px;
    color: white;
}

.qualification_percentage {
    font-size: 20px;
    color: white;
}

.qualification_round {
    display: inline-block;
    width: 13px;
    height: 13px;
    background-color: whitesmoke;
    border-radius: 50%
}

.qualification_line {
    display: block;
    width: 1px;
    height: 100%;
    background-color: #f9ab00;
    -webkit-transform: translate(6px, -7px);
            transform: translate(6px, -7px);
}


@media screen and (max-width:480px) {
    .data:nth-child(1) {
        text-align: start;
    }

    .data:nth-child(1) h3 {
        text-align: start;
    }

    .qualification_content {
        height: 33rem;
    }

    .qualification_content::before {
        content: "";
        position: absolute;
        height: 33rem;
        width: 1px;
        background-color: whitesmoke;
    }

    .qualification_data {
        display: block;
        margin-bottom: 25px;
        padding-left: 20px;
    }

    .qualification_data::before {
        content: "";
        position: absolute;
        height: 15px;
        width: 15px;
        border-radius: 50%;
        background-color: #f9ab00;
        -webkit-transform: translate(-27px);
                transform: translate(-27px)
    }

    .qualification_subtitle {
        display: inline-block;
        font-size: 18px;
        font-weight: 300;
        color: white;
    }

    .qualification_calendar {
        font-size: 18px;
        color: white;
    }

    .qualification_percentage {
        font-size: 18px;
        color: white;
    }

    .qualification_round {
        display: none;
        width: 13px;
        height: 13px;
        background-color: whitesmoke;
        border-radius: 50%
    }

    .qualification_line {
        display: none;
        width: 1px;
        height: 100%;
        background-color: whitesmoke;
        -webkit-transform: translate(6px, -7px);
                transform: translate(6px, -7px);
    }
}
